import React, { useState, useEffect } from "react"
import { Toast } from "react-bootstrap"
import { connect } from "react-redux"
import { notificationDataRequest } from "./redux/actions"

import { onMessageListener } from "../../firebase"

import { onMessage } from "firebase/messaging"
import messaging from "../../firebase"

const ForegroundNotification = props => {
  const [show, setShow] = useState(false)
  const [messageData, setMessageData] = useState(null)

  useEffect(() => {
    onMessage(messaging, payload => {
      // console.log("Message received. ", payload)
      setMessageData(payload.notification)
      setShow(true)
      // notificationDataRequest(payload.notification)
      // getNotficationDataRequest()
    })
  }, [messaging])

  return (
    <>
      {messageData && (
        <Toast
          onClose={e => {
            setShow(false)
            setMessageData(null)
          }}
          show={show}
          delay={3000}
          autohide
          animation
          style={{
            position: "absolute",
            zIndex: 999,
            top: "15%",
            right: "20px",
            // margin: '0px auto',
            minWidth: 200
            // backgroundColor: 'red'
          }}
        >
          <Toast.Header>
            <img
              src="holder.js/20x20?text=%20"
              className="rounded mr-2"
              alt=""
            />
            <strong className="mr-auto">{messageData.title}</strong>
            <small>just now</small>
          </Toast.Header>
          <Toast.Body>{messageData.body}</Toast.Body>
        </Toast>
      )}
    </>
  )
}

export default ForegroundNotification
